
'use server';
/**
 * @fileOverview A Genkit flow for submitting user feedback to Firestore.
 */

import { ai } from '@/ai/genkit';
import { adminDb } from '@/lib/firebase-admin';
import { z } from 'genkit';
import { Timestamp } from 'firebase-admin/firestore';

// Define the Zod schema for the feedback form input
const FeedbackSchema = z.object({
  name: z.string().optional().describe('The name of the user providing feedback.'),
  email: z.string().optional().describe('The email of the user.'),
  rating: z.number().min(1).max(5).describe('The user\'s star rating from 1 to 5.'),
  feedback: z.string().min(10).describe('The user\'s detailed feedback text.'),
});

export type Feedback = z.infer<typeof FeedbackSchema>;

// Define the Zod schema for the flow's output
const FlowOutputSchema = z.object({
  success: z.boolean(),
  error: z.string().optional(),
});
export type FlowOutput = z.infer<typeof FlowOutputSchema>;

export async function submitFeedback(feedback: Feedback): Promise<FlowOutput> {
  return submitFeedbackFlow(feedback);
}


const submitFeedbackFlow = ai.defineFlow(
  {
    name: 'submitFeedbackFlow',
    inputSchema: FeedbackSchema,
    outputSchema: FlowOutputSchema,
  },
  async (feedback) => {
    // Check if the Firestore database connection is available
    if (!adminDb) {
      console.error(
        'CRITICAL: Firestore database is not initialized. Check firebase-admin.ts for errors.'
      );
      // This is a server configuration error, so we return a specific message.
      return {
        success: false,
        error: 'The server is not configured to save feedback. Please contact support.',
      };
    }

    try {
      // Add the user's feedback to the 'feedback' collection in Firestore
      await adminDb.collection('feedback').add({
        ...feedback,
        // Add a server-side timestamp for when the feedback was received
        submittedAt: Timestamp.now(),
      });
      
      // Return a success response
      return { success: true };
    } catch (e: any) {
      console.error('Error submitting feedback to Firestore:', e);
      // This is a runtime error during the Firestore operation.
      return {
        success: false,
        error: 'An unexpected error occurred while saving your feedback.',
      };
    }
  }
);
